<template>
  <svg :class="svgClass" :style="{ width: size, height: size, color: color }" aria-hidden="true">
    <use :xlink:href="`#icon-${name}`" />
  </svg>
</template>
<script>
// 
// https://blog.csdn.net/m0_66746443/article/details/123381448
export default {
  name: 'svg-icon',
  props: {
    name: { type: String, required: true },     // svg 
    className: { type: String, default: '' },   // 
    size: { type: String, default: '20px' },        // 
    color: { type: String, default: "inherit" }    // 
  },
  computed: {  // 
    svgClass () {
      if (this.className) {
        return 'svg-icon ' + this.className
      } else {
        return 'svg-icon'
      }
    }
  }
}
</script>

<style scoped>
.svg-icon {
  fill: currentColor;
  overflow: hidden;
}
</style>